.profile-container {
  font-family: 'Inter', sans-serif;
  background-color: #fdfdfd;
  padding: 20px;
  padding-bottom: 0px; /* Bottom padding ensures footer doesn't overlap */
  height: 100vh;
  overflow-y: auto;
  }
  
 
  
  .home-divider {
    width: 100%;
    height: 1px;
    background-color: #ddd; /* Light gray color for the divider */
    margin: 0px 0; /* Space above and below the divider */
    margin-bottom: 10px;
  }
  
  
  .profile-header {
    text-align: center;
    margin-bottom: 1.5rem;
  }
  
  .profile-image-wrapper {
    position: relative;
    display: inline-block;
  }
  
  .profile-image {
    width: 10rem;
    height: 10rem;
    border-radius: 50%;
    margin-top: 10px;
  }
  
  .selleredit-button {
    position: absolute;
    bottom: 0;
    right: 0;
    background: rgb(255, 255, 255);
    border: 1px solid #d1d5db;
    border-radius: 50%;
    padding: 0.5rem;
   
  }
  
  .name {
    margin-top: 0.5rem;
    font-size: 1.25rem;
    font-weight: 900;
    color: #272626;
  }
  
  .contact {
    margin-top: -10px;
    color: #6b7280;
    font-size: 0.875rem;
  }
  
  .stats-container {
    display: flex;
    justify-content: space-evenly;
    margin-top: 1rem;
  }
  
  .stat {
    text-align: center;
    background-color: #ffffff; /* White background for the stat */
    padding: 1rem 1.5rem; /* Spacious padding */
    border-radius: 1rem; /* Rounded corners for a smooth, modern look */
    width: 48%; /* Ensures stats are evenly spaced */
    box-shadow: 0 6px 18px rgba(0, 0, 0, 0.1); /* More intense shadow for better visibility */
    background: linear-gradient(145deg, #f7f8f9, #e9ecef); /* Soft gradient for a white-on-white look with a hint of color */
    margin: 0.75rem; /* Small margin to create space between stats */
    border: 1px solid #e0e0e0;
    font-family: 'Inter', sans-serif;
  }
  
  
  
  .stat-value {
    font-size: 2rem;
    font-weight: 700;
    color: #111827;
    font-family: 'Inter', sans-serif;
  }
  
  .stat-label {
    font-size: 0.875rem;
    color: #6b7280;
    font-family: 'Inter', sans-serif;
  }
  
  .action-button {
    width: 100%;
    display: flex;
    align-items: center;
    padding: 0.70rem;
    border-radius: 0.375rem;
    font-size: 1rem;
    margin-top: 0.75rem;
    cursor: pointer;
    border: 1px solid #e0e0e0; /* Subtle grey border */
    color: #374151;
    background-color: rgba(243, 244, 246, 1);
    font-weight: 600;
    font-family: 'Inter', sans-serif;
  }
  
  
  .action-button:hover {
    background-color: rgba(229, 231, 235, 1);
  }
  
  .action-button.danger {
    color: #ffffff;
    background-color: #ef4444;
  }
  
  .action-button.danger:hover {
    background-color: #dc2626;
  }
  
  .action-icon {
    margin-top: 5px;
    font-size: 1.25rem;
    margin-right: 0.75rem;
  }
  
  .action-icon.danger {
    color: #ffffff;
  }
  /* Footer styles */
.footer {
  display: flex;
  justify-content: space-between;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #fff;
  border-top: 1px solid #ccc;
  padding: 10px 20px;
}

.footer-button {
  text-align: center;
  font-size: 0.9rem;
  color: #555;
  cursor: pointer;
}

.footer-button.active {
  color: #000;
  font-weight: bold;
}